<template>
  <div class="login">
    <div class="login-box">
      <van-nav-bar @click-right="goHome">
        <template #left>
          <div class="left-text">
            <div class="logo">
              <img class="auto-img" src="../assets/img/home_active.png" alt />
            </div>
            <div class="logo-title">Luckin Coffee</div>
          </div>
        </template>
        <template #right>
          <div class="close-box">先逛一逛</div>
        </template>
      </van-nav-bar>

      <div class="login-form">
        <div class="title">欢迎回来!</div>
        <div class="en-title">Please login to your accounts</div>

        <div class="login-form-box">
          <van-form>
            <van-field label="手机号" placeholder="手机号" v-model="userLoginInfo.phone" />
            <van-field
              :type="isPassword ? 'password' : 'text'"
              label="密码"
              placeholder="密码"
              v-model="userLoginInfo.password"
              :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon="viewPassword"
            />
            <div class="clearfix forgot">
              <span class="fr" @click="goPage('Forgot')">忘记密码?</span>
            </div>
            <div class="login-btn">
              <van-button @click="login" round block type="info" native-type="submit">登&nbsp;&nbsp;录</van-button>
            </div>
            <div class="login-btn">
              <van-button @click="showPopup" round block type="default">注&nbsp;&nbsp;册</van-button>
            </div>
          </van-form>
        </div>
      </div>
    </div>
    <van-popup v-model="show" closeable position="bottom" :style="{ height: '50%' }">
      <div class="login-form-popup">
        <van-form>
          <van-field v-model="userRegisterInfo.nickName" required label="用户名" placeholder="请输入用户名" />
          <van-field required label="手机号" placeholder="11位手机号" v-model="userRegisterInfo.phone" />
          <van-field
            required
            @input="comPassword"
            :type="isPassword ? 'password' : 'text'"
            label="密码"
            placeholder="6-16位密码且以字母开头"
            v-model="userRegisterInfo.password"
          />
          <van-field
            @input="comPassword"
            required
            :type="isPassword ? 'password' : 'text'"
            label="确认密码"
            placeholder="确认密码"
            v-model="userRegisterInfo.ifpassword"
            :error-message="message"
          />

          <div class="login-btn">
            <van-button
              round
              block
              @click="register"
              type="info"
              native-type="submit"
            >注&nbsp;&nbsp;册</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/Login.css";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      show: false,
      message: "",
      userLoginInfo: {
        phone: "",
        password: "",
      },
      userRegisterInfo: {
        phone: "",
        nickName: "",
        password: "",
        ifpassword: "",
      },

      isPassword: true,
    };
  },

  methods: {
    goPage(name) {
      this.$router.push({ name });
    },
    goHome() {
      
      this.$router.push({ name: "Home" });
    },
    viewPassword() {
      this.isPassword = !this.isPassword;
    },
    showPopup() {
      // 
      this.show = true;
    },
    comPassword() {
      if (this.userRegisterInfo.password == "") {
        this.message = "请先填写密码";
      } else {
        if (
          this.userRegisterInfo.password == this.userRegisterInfo.ifpassword
        ) {
          this.message = "密码正确";
          // 
        } else {
          this.message = "密码不一样";
          // 
        }
      }
      if (this.userRegisterInfo.ifpassword == "") {
        this.message = "";
      }
    },

    register() {
      let userInfo = this.userRegisterInfo;
      for (let key in userInfo) {
        if (userInfo[key] == "") {
          return this.$notify({
            type: "warning",
            message: "请完善信息",
          });
        }
      }
       if (
          userInfo.password == userInfo.ifpassword
        ) {
        
        } else {
          return this.$notify({
            type: "warning",
            message: "请再次确认密码",
          });
        }

      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机格式不对",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "昵称格式不正确",
        },
      };

      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      
      this.axios({
        method: "POST",
        url: "/register",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 100) {
            this.show = false;
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });

            for (let key in userInfo) {
              userInfo[key] = "";
            }
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    login() {
      let userInfo = this.userLoginInfo;

      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };
      if (!validForm.valid(o)) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);

      this.axios({
        method: "POST",
        url: "/login",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          
          if (result.data.code == 200) {
            localStorage.setItem("CSTK", result.data.token);
            // 跳转到首页home页面
            this.$router.push({ name: "Home" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();

          
        });
    },
  },
};
</script>

